//XW:,''
export function trans_params (cell) {
  let reg = new RegExp('\{(.*?)\}', 'g');
  let arg = cell.f.match(reg);
  if (arg !== null){
    for (let i=0; i<arg.length; i++){
      let param = arg[i];
      let rep = "'" + param + "'";
      cell.f = cell.f.replace(param, rep)
    }
  }
  return cell
};

export function recover_sheet(sheet) {
  Object.keys(sheet).forEach(i => {
    if (sheet[i].v === 'default_0'){
      if (!helper.isHave(sheet[i].f)) {
        sheet[i].f = ""
      }
      sheet[i].v = 0
    }
  });
  return sheet
};
//XW: end
//XW: 
export function strim(str){
  let str1 = str.replace(/\s+$/,'');
  let str2 = str1.replace(/^\s+/,'');
  return str2;
};
//XW end

//xW: 
export function trans_formula(f){
  // FLOOR->FLOORMATH, vap-> vara
  let trans_dict = {
    'FLOOR': 'FLOORMATH',
    'let': 'VARA',
    'WORKDAY.INTL': 'WORKDAYINTL'
  };
  for(let i in trans_dict){
    let b = trans_dict[i];
    if (f.indexOf(b) < 0 && f.indexOf(i)>=0 && f.indexOf(i + '.') <=0){
      f = f.replace(i, b)
    }
  }
  //_XLFN. 
  f = f.replace('_XLFN.', '');
  //""
  let reg = new RegExp('"(.*?)"', 'g');
  let arg = f.match(reg);
  if (f.indexOf('=') === 0){
    f = f.toUpperCase();
    if (arg !== null) {
      for (let i = 0; i < arg.length; i++) {
        let param = arg[i].toUpperCase();
        if(f.indexOf(param) >= 0){
          f = f.replace(param, arg[i])
        }
      }
    }
  }
  return f
}
//xW:end
